.Trends {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: var(--cardColor);
    padding: 1rem;
    border-radius: 1rem;
    padding-left: 2rem;
}
.trend {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}
.trend > span:nth-of-type(1) {
    font-weight: bold;
}
.trend > span:nth-of-type(2) {
    font-size: 12px;
}
.r-button {
    width: 80%;
    height: 3rem;
    align-self: center;
}
