.postShare {
    display: flex;
    /* flex-direction: column; */
    gap: 1rem;
    background-color: var(--cardColor);
    padding: 1rem;
    border-radius: 1rem;
}
.postShare > img {
    border-radius: 50%;
    height: 3rem;
    width: 3rem;
}
.postShare > div {
    display: flex;
    flex-direction: column;
    width: 90%;
    gap: 1rem;
}
.postShare > div > input {
    background-color: var(--inputColor);
    border-radius: 10px;
    padding: 10px;
    font-size: 17px;
    border: none;
    outline: none;
}
.postOptions {
    display: flex;
    justify-content: space-around;
}
.option {
    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 5px;
    display: flex;
    /* flex-direction: row; */
    justify-content: center;
    align-items: center;
    font-size: 12px;
}
.option:hover {
    cursor: pointer;
}
.ps-button {
    padding: 5px;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 12px;
}
.prevImage {
    position: relative;
}
.prevImage > svg {
    position: absolute;
    right: 1rem;
    top: 0.5rem;
    cursor: pointer;
}
.prevImage > img {
    width: 100%;
    max-height: 20rem;
    object-fit: cover;
    border-radius: 0.5rem;
}
